import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Container, Paper, Typography, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button, FormControl, InputLabel, Select, MenuItem } from '@mui/material';
import TrendChart from './TrendChart';
import { Link as RouterLink } from 'react-router-dom';


const groupResultsByRunId = (results) => {
  // Group by run_id (timestamp) and project
  const grouped = {};
  results.forEach(r => {
    let runLabel = r.run_id;
    let dateStr = runLabel;
    if (/^\d{13,}$/.test(runLabel)) {
      const date = new Date(Number(runLabel));
      dateStr = date.toLocaleString('en-GB', { year: 'numeric', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit' }).replace(/\//g, '-').replace(',', '');
    }
    const project = r.framework || 'Unknown';
    const key = `${runLabel}__${project}`;
    if (!grouped[key]) grouped[key] = { date: dateStr, project, passed: 0, failed: 0 };
    if (r.status === 'passed') grouped[key].passed += 1;
    if (r.status === 'failed') grouped[key].failed += 1;
  });
  return Object.values(grouped);
};


export default function Dashboard({ role }) {
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedProject, setSelectedProject] = useState('All');

  const fetchResults = () => {
    setLoading(true);
    axios.get('http://localhost:5000/api/results')
      .then(res => {
        setResults(res.data);
        setLoading(false);
      })
      .catch(() => setLoading(false));
  };

  useEffect(() => {
    fetchResults();
  }, []);

  // Get unique project names
  const projectNames = Array.from(new Set(results.map(r => r.framework || 'Unknown')));
  const filteredResults = selectedProject === 'All' ? results : results.filter(r => (r.framework || 'Unknown') === selectedProject);
  const groupedResults = groupResultsByRunId(filteredResults);

  if (loading) return <div>Loading...</div>;
  return (
    <Container maxWidth="lg" sx={{
      background: 'linear-gradient(135deg, #e3f0ff 0%, #f4f6fa 100%)',
      minHeight: '100vh',
      py: 4
    }}>
      <TrendChart groupedResults={groupedResults} />
      <Paper elevation={3} sx={{ p: 6, mt: 8 }}>
        <Typography variant="h6" align="left" gutterBottom>Date Wise Test Results</Typography>
        <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
          <FormControl sx={{ minWidth: 200, mb: 2 }} size="small">
            <InputLabel id="project-filter-label">Select Project</InputLabel>
            <Select
              labelId="project-filter-label"
              value={selectedProject}
              label="Select Project"
              onChange={e => setSelectedProject(e.target.value)}
            >
              <MenuItem value="All">All</MenuItem>
              {projectNames.map((name, idx) => (
                <MenuItem key={idx} value={name}>{name}</MenuItem>
              ))}
            </Select>
          </FormControl>
          <Button variant="outlined" sx={{ mb: 2, height: 40 }} onClick={fetchResults}>Refresh</Button>
        </div>
        <Typography variant="body2" sx={{ mb: 2, color: 'gray' }}>Showing last 30 Executions Records</Typography>
        <TableContainer component={Paper} sx={{ mt: 4 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Date</TableCell>
                <TableCell>Project Name</TableCell>
                <TableCell>Pass</TableCell>
                <TableCell>Fail</TableCell>
                <TableCell>Total</TableCell>
                <TableCell>Pass %</TableCell>
                <TableCell>Reports</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {groupedResults.map((row, idx) => {
                const total = row.passed + row.failed;
                const passPercent = total > 0 ? ((row.passed / total) * 100).toFixed(1) : '0.0';
                return (
                  <TableRow key={idx}>
                    <TableCell>{row.date}</TableCell>
                    <TableCell>{row.project}</TableCell>
                    <TableCell>{row.passed}</TableCell>
                    <TableCell>{row.failed}</TableCell>
                    <TableCell>{total}</TableCell>
                    <TableCell>{passPercent}%</TableCell>
                    <TableCell>
                      <Button variant="outlined" size="small" component={RouterLink} to={`/testrun/${encodeURIComponent(row.date)}/${encodeURIComponent(row.project)}`}>View Details Report</Button>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Container>
  );
}

