import React from 'react';
import { Paper, Typography } from '@mui/material';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

export default function TrendChart({ groupedResults }) {
  // Sort by date (assume date is string, convert to Date for sorting)
  const sorted = [...groupedResults].sort((a, b) => new Date(a.date) - new Date(b.date)).slice(-30);
  const data = {
    labels: sorted.map(r => r.date),
    datasets: [
      {
        label: 'Passed',
        data: sorted.map(r => r.passed),
        backgroundColor: 'rgba(34,197,94,0.7)'
      },
      {
        label: 'Failed',
        data: sorted.map(r => r.failed),
        backgroundColor: 'rgba(239,68,68,0.7)'
      }
    ]
  };
  const options = {
    responsive: true,
    plugins: {
      legend: { position: 'top' },
      title: { display: true, text: 'Historical Automation Pass/Fail Trend (Last 30 Executions)' }
    },
    scales: {
      x: { stacked: true },
      y: { stacked: true, beginAtZero: true }
    }
  };
  return (
    <Paper elevation={3} sx={{ p: 3, mb: 4 }}>
      <Bar data={data} options={options} />
    </Paper>
  );
}
